
import React,{useState, useEffect} from 'react';
import '../../style/main.scss'
import {Link} from 'react-scroll';


const Navbar = () =>{
    const[navbar,setNavbar] = useState(false);

    const changeBackground = () =>{
        if(window.scrollY >= 150){
            setNavbar(true);
        } else {
            setNavbar(false);
        }
    };

    window.addEventListener('scroll',changeBackground);
        return(
                <nav className={navbar ? 'navbar active' : 'navbar'}>
                    <ul className="nav_menu" data-aos="fade-up" data-aos-anchor-placement="top-bottom">
                        <li className="nav_menu_list"> 
                        <Link to="me" smooth={true} duration={1300}> Me </Link> 
                        </li>
                        <li className="nav_menu_list">
                        <Link to="works" smooth={true} duration={1300}> Works </Link> 
                        </li>
                        <li className="nav_menu_list">
                        <Link to="blog" smooth={true} duration={1300}> Blog </Link> 
                        </li>
                        <li className="nav_menu_list">
                        <Link to="ask" smooth={true} duration={1300}> Ask </Link> 
                        </li>
                    </ul>
                </nav>
    )
}

export default Navbar;

